<template>
	<v-layout justify-center align-center class="countdown-wrapper" id="countdown-box">
		<v-row v-if="isInFuture">
			<v-col cols="12">
				<h1 class="countdown-title">Do prižiga še</h1>
			</v-col>
			<v-col cols="12">
				<v-row class="counter-row">
					<v-col cols="2" class="py-0"><p class="time-text">{{time.day}}</p></v-col>
					<v-col cols="2" class="py-0"><p class="time-text">{{time.hour}}</p></v-col>
					<v-col cols="2" class="py-0"><p class="time-text">{{time.minute}}</p></v-col>
					<v-col cols="2" class="py-0"><p class="time-text">{{time.second}}</p></v-col>
				</v-row>
				<v-row class="counter-row">
					<v-col cols="2" class="py-0"><p class="time-label">{{labels.day}}</p></v-col>
					<v-col cols="2" class="py-0"><p class="time-label">{{labels.hour}}</p></v-col>
					<v-col cols="2" class="py-0"><p class="time-label">{{labels.minute}}</p></v-col>
					<v-col cols="2" class="py-0"><p class="time-label">{{labels.second}}</p></v-col>
				</v-row>
			</v-col>
		</v-row>
		<v-row v-else>
			<v-col cols="12">
				<h1>Lučke gorijo!</h1>
			</v-col>
		</v-row>
	</v-layout>
</template>

<script>
	require('moment-countdown');
	let moment = require('moment');

	export default {
		name: "CountDown",
		data() {
			return {
				timer: '',
				diff: null,
				interval: null,
				eventStartDate: moment('2020-12-12 18:00'),
				snowFallInterval: null,
				time: {
					day: '',
					hour: '',
					minute: '',
					second: ''
				},
				labels: {
					day: 'dni',
					hour: 'ur',
					minute: 'minut',
					second: 'sekund'
				}
			}
		},
		created() {
			const vm = this;

			vm.countDown();
		},
		mounted() {
			const vm = this;

			/* vm.snowFallInterval = setInterval(() => {
				vm.createSnowFlake();
			}, 200); */
		},
		destroyed() {
			const vm = this;

			clearTimeout(vm.interval);
			vm.interval = null;

			/*clearInterval(vm.snowFallInterval);
			vm.snowFallInterval = null;*/
		},
		computed: {
			isInFuture() {
				const vm = this;

				return vm.diff <= 0;
			}
		},
		methods: {
			countDown() {
				const vm = this;

				let endString = '';

				const count = vm.eventStartDate.countdown();

				vm.diff = count.value;

				vm.time.day = count.days;

				if (count.days === 1) vm.labels.day = 'dan';
				else if (count.days === 2) vm.labels.day = 'dneva';
				else vm.labels.day = 'dni';

				vm.time.hour = count.hours;

				if (count.hours === 1) vm.labels.hour = 'ura';
				else if (count.hours === 2) vm.labels.hour = 'uri';
				else if (count.hours === 3 || count.hours === 4) vm.labels.hour = 'ure';
				else vm.labels.hour = 'ur';

				vm.time.minute = count.minutes;

				if (count.minutes === 1) vm.labels.minute = 'minuta';
				else if (count.minutes === 2) vm.labels.minute = 'minuti';
				else if (count.minutes === 3 || count.minutes === 4) vm.labels.minute = 'minute';
				else endString += vm.labels.minute = 'minut';

				vm.time.second = count.seconds;

				if (count.seconds === 1) vm.labels.second = 'sekunda';
				else if (count.seconds === 2) vm.labels.second = 'sekundi';
				else if (count.seconds === 3 || count.seconds === '4') vm.labels.second = 'sekunde';
				else vm.labels.second = 'sekund';

				//vm.timer = count.toString();
				vm.timer = endString;

				clearTimeout(vm.interval);
				vm.interval = setTimeout(() => vm.countDown(), 200);
			},
			createSnowFlake() {
				let container = document.getElementById('countdown-box');

				const snowFlake = document.createElement('i');
				snowFlake.classList.add('fa');
				snowFlake.classList.add('fa-snowflake-o');
				snowFlake.classList.add('snowflake-item');
				snowFlake.style.left = container.offsetLeft + Math.random() * container.offsetWidth + 'px';
				snowFlake.style.animationDuration = Math.random() * 5 + 5 + 's';
				snowFlake.style.opacity = Math.random();
				snowFlake.style.fontSize = Math.random() * 10 + 10 + 'px';

				setTimeout(() => {
					snowFlake.remove();
				}, 10000);

				container.appendChild(snowFlake);
			}
		}
	}
</script>

<style lang="scss">
	.countdown-wrapper {
		background-color: #EF4758;
		border-radius: 20px;
		width: 60%;
		height: 60%;
	}

	.countdown-title {
		color: white;
		text-align: center;
		font-family: 'Charm' !important;
	}

	.counter-row {
		text-align: center;
		align-items: center;
		justify-content: center;
	}

	.time-text {
		color: white;
		font-size: 30px;
		font-weight: bold;
		margin-bottom: 0 !important;
	}

	.time-label {
		color: white;
		font-weight: lighter;
		font-size: 20px;
		margin-bottom: 0 !important;
	}

	.snowflake-item {
		animation: fall linear forwards;
		color: #ffffff;
		position: absolute;
		top: -20px;
	}

	@keyframes fall {
		to {
			transform: translateY(70vh);
		}
	}

	/* Extra small devices (phones, 600px and down) */
	@media only screen and (max-width: 600px) {
		.snowflake-item {
			animation: fall linear forwards;
			color: #ffffff;
			position: absolute;
			top: 300px;
		}
	}

	/* Small devices (portrait tablets and large phones, 600px and up) */
	@media only screen and (min-width: 600px) {

	}

	/* Medium devices (landscape tablets, 768px and up) */
	@media only screen and (min-width: 768px) {

	}

	/* Large devices (laptops/desktops, 992px and up) */
	@media only screen and (min-width: 992px) {

	}
</style>
