<template>
    <v-app id="main-app">
        <div id="page-header" class="page-header">
            <img src="./assets/img/Christmas-page-header.jpg" class="header-image">
        </div>

        <v-main>
            <main-page/>
        </v-main>

        <v-footer padless color="#EF4758" id="page-footer">
            <v-col
                    class="text-center"
                    cols="12"
            >
                <p class="footer-text">&#169{{ new Date().getFullYear() }} — <strong>Bezovšek family</strong></p>
            </v-col>
        </v-footer>
    </v-app>
</template>

<script>

    import MainPage from "./views/MainPage";

    export default {
        name: 'App',

        components: {
            MainPage

        },

        data: () => ({
            snowFallInterval: null
        }),

        created() {
            const vm = this;
        },
        mounted() {
            const vm = this;

            vm.snowFallInterval = setInterval(() => {
                vm.createSnowFlake();
            }, 50);

            const appHeight = () => {

                let body = document.body;
                let html = document.documentElement;

                let height = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);

                const doc = document.documentElement
                doc.style.setProperty('--app-height', `${height}px`)
            }
            window.addEventListener('resize', appHeight);
            appHeight()
        },
        destroyed() {
            const vm = this;

            clearInterval(vm.snowFallInterval);
            vm.snowFallInterval = null;
        },
        methods: {
            createSnowFlake() {
                let container = document.getElementById('main-app');

                const snowFlake = document.createElement('i');
                snowFlake.classList.add('fa');
                snowFlake.classList.add('fa-snowflake-o');
                snowFlake.classList.add('snowflake-item-header');
                snowFlake.style.left = container.offsetLeft + Math.random() * container.offsetWidth + 'px';
                snowFlake.style.animationDuration = Math.random() * 5 + 5 + 's';
                snowFlake.style.opacity = Math.random();
                snowFlake.style.fontSize = Math.random() * 10 + 10 + 'px';

                setTimeout(() => {
                    snowFlake.remove();
                }, 10000);

                container.appendChild(snowFlake);
            }
        }
    };
</script>

<style lang="scss">

    :root {
        --app-height: 100%;
    }

    .header-image {
        width: 100%;
    }

    .v-main {
        padding: 0 !important;
    }

    .v-footer {
        z-index: 10 !important;
    }

    .footer-text {
        color: white;
        margin: 0 !important;
    }

    .snowflake-item-header {
        animation: snowfall linear forwards;
        color: #ffffff;
        position: absolute;
        top: -20px;
        z-index: 9 !important;
    }

    @keyframes snowfall {
        to {
            transform: translateY(100vh);
        }
    }

    /* Extra small devices (phones, 600px and down) */
    @media only screen and (max-width: 600px) {
        @keyframes snowfall {
            to {
                transform: translateY(var(--app-height));
            }
        }
    }

    /* Small devices (portrait tablets and large phones, 600px and up) */
    @media only screen and (min-width: 600px) {

    }

    /* Medium devices (landscape tablets, 768px and up) */
    @media only screen and (min-width: 768px) {

    }

    /* Large devices (laptops/desktops, 992px and up) */
    @media only screen and (min-width: 992px) {

    }
</style>
